@import url(https://fonts.googleapis.com/css2?family=Comic+Neue:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comic+Neue:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Be+Vietnam+Pro:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Exo:wght@300&display=swap);
html {
    scroll-behavior: smooth;
}

body {
    margin: 0;
    padding: 0;
    background: #000000;
}

@font-face {
    font-family: ExoBlack;
    src: url(/monkey-theme-website/static/media/Exo-Black.938f9ec1.ttf);
}

@font-face {
    font-family: ExoThin;
    src: url(/monkey-theme-website/static/media/Exo-Thin.876e4e09.ttf);
}

@font-face {
    font-family: ExoRegular;
    src: url(/monkey-theme-website/static/media/Exo-Regular.609aaa02.ttf);
}

@media only screen and (max-width: 600px) {
    html, body {
        width: 600px;
    }
}


@media only screen and (max-width: 400px) {
    html, body {
        width: 100%;
    }
}

iframe {
    z-index: -1 !important;
}
.App {
  text-align: center;
}

.masthead {
    height: 100vh;
    min-height: 500px;
    /* background-image: url("../../assets/image/banner.png"); */
    /* background-size: cover;
    background-position: center;
    background-repeat: no-repeat; */
}

.wrap {
    font-family: 'Exo', sans-serif;
    color: white;
}

.desc {
    text-align: justify;
}

.img {
    border-radius: 10px;
}

.t-shadow {
    /* text-shadow: 6px 6px #131212; */
}

@media (min-width: 1000px) {
    .welcome {
        font-size: 3rem;
    }

    .tittle {
        font-size: 5rem;
    }
}

@media (max-width: 1000px) {
    .masthead {
        margin-top: 40px;
    }

    .welcome {
        font-size: 2rem;
    }
}
@media (max-width: 900px) {
    .masthead {
        height: auto;
    }
    .tittle {
        text-align: center !important;
        font-size: 32px;
    }
}
